* {
    padding:0;
    margin:0;
    font-family:Helvetica;
}

.outter-layout { 
    padding:20px;
    /* background-color:var(--primary); */
}

.description  {
    padding:10px 0;
    font-size:16px;
}
.inner-desc { 
    display:flex;
    flex-direction : row;
    color:white;
}
.inner-desc > .key-desc {
    min-width:30%;
}

.key-desc { 
    padding-right:5px;
}

.desc-header { 
    color : #23EB41;
    font-size : 24px;
    color:white;
    padding:4px 0 ;
}
.description-wrapper {
    padding:16px;
    background-color: rgb(62, 66, 69);
    border-radius : 12px;
    /* max-height:160px; */
    margin:8px;
    /* background-color : #23EB41 */
}
.text {
    margin-top : 12px;
}

img {
    width:100%;
    object-fit:cover;
    border-radius:10px;
}
.panel {
    /* background-color:#3E4245; */
    background-color:#6E757A;
    border-radius:10px;
      /* grid-row: 1 / -1; */
      /* grid-column: 1; */
}

.breeds { 
    display:grid;
    grid-gap :16px;
    grid-template-columns : repeat(auto-fill,minmax(380px,1fr));

}
